import React, { useEffect, useState } from 'react'
import { Layout, AvailableServices } from '../index'
import { withPrefix } from 'gatsby'
import styles from './request.module.css'
import { useForm } from 'react-hook-form'
export default () => {

    const [serviceName, setServiceName] = useState()

    const { register, handleSubmit } = useForm()

    useEffect(() => {
        const queryString = window.location.search

        const urlParams = new URLSearchParams(queryString)

        const service = urlParams.get('service')

        setServiceName(service)
    }, [])

    const onSubmit = (data) => {
        console.log(data)

        MakeRequest({
            serviceName: serviceName,
            phoneNum: data.phoneNum.replace("-",""),
            address: data.address,
            extraAddress: data.extraAddress,
            memo: data.memo
        })
    }

    return (
        <Layout>
            {
                serviceName ?
                    <div className={styles.requested_service}>
                        <img src={withPrefix(`/aquaman/${serviceName}.jpg`)} />
                        <h2 className={styles.service_name}>{serviceName}</h2>
                        <div className={styles.cost_container}>
                            <p className={styles.cost}> <strong>{serviceInfo[serviceName].installCost}</strong></p>
                            <p className={styles.cost}>// <strong>{serviceInfo[serviceName].defaultCost}</strong></p>
                            <p className={styles.cost_caption}>*   ,     </p>
                            <p className={styles.total_cost}> <strong>{serviceInfo[serviceName].totalCost}</strong></p>
                        </div>

                        <form className={styles.form} onSubmit={handleSubmit(onSubmit)}>
                            <label htmlFor={"address"}></label>
                            <input placeholder={"//"} type="text" name={"address"} ref={register} />

                            <label htmlFor={"address"}></label>
                            <input placeholder={""} type="text" name={"extraAddress"} ref={register} />

                            <label htmlFor={"address"}></label>
                            <input placeholder={"-   "} type="text" name={"phoneNum"} ref={register} />

                            <label htmlFor={"memo"}> </label>
                            <input type="text" name={"memo"} ref={register} />

                            <button type="submit">  </button>
                        </form>
                    </div>
                    :
                    <AvailableServices title={" "} />
            }
        </Layout>
    )
}

const MakeRequest = ({ serviceName, phoneNum, address, extraAddress, memo }) => {

    const endpoint = "https://api1.hellomy.house/graphql";
    const query = `mutation { 
                requestConsulting(
                    serviceName: "${serviceName}",
                    mainAddr: "${address}",
                    subAddr: "${extraAddress}", 
                    phoneNum: "${phoneNum}",
                    memo: "${memo}")
                }`

    try {
        fetch(endpoint, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ query: query })
        }).then((d) => {
            d.json()
        }).then(res => { 
            window.location = "/aquaman"
        })
    } catch (e) {
        console.log(e)
    }
}

